import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";
import Seo from "../components/seo";
import styled from "styled-components";
import { useSiteMetadata } from "../hooks/use-site-metadata";

const Timeline = styled.div`
  &:before {
    content: "";
    position: absolute;
    height: calc(100% + 6rem);
    border: 1px solid #eb219b;
    left: 0px;
    top: -3rem;
  }

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  @media screen and (min-width: 768px) {
    &:before {
      left: calc(4.5rem - 2px);
      right: auto;
    }
  }
`;
const TimelineItem = styled.div`
  position: relative;
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
  }
`;

const JobContainer = styled.div`
  margin-bottom: 4rem;
  .subtitle {
    margin: 0;
  }
  padding: 1rem;
  border: 2px solid #eb219b;
  border-image: linear-gradient(to right, #a400ff 0%, #eb219b 100%);
  border-image-slice: 1;
  position: relative;
  clear: both;
  width: 100%;

  @media screen and (min-width: 768px) {
    margin: 2rem 0rem 2rem 3rem;
    &:after,
    &:before {
      content: "";
      position: absolute;
    }

    &:before {
      left: -10px;
      top: calc(50% - 5px);
      border-style: solid;
      border-color: #a400ff #a400ff transparent transparent;
      border-width: 10px;
      transform: rotate(225deg);
    }
  }

  ul {
    list-style-type: disc;
    margin-left: 1rem;
  }
`;

const YearContainer = styled.span`
  position: relative;
  background-color: #a400ff;
  color: white;
  padding: 0.5rem;
  display: inline-block;
  @media screen and (min-width: 768px) {
    padding: 1rem;
  }
`;

const getYear = (date) => {
  return date.split("-")[0];
};

const Experiences = ({ data }) => {
  const { cvExperiences, cvAchievements, cvSkillsUsed, cvSecteur, cvTeam } =
    useSiteMetadata();
  return (
    <Layout title={cvExperiences}>
      <Seo title={cvExperiences} htmlclass="" />
      <section className="section">
        <div className="container">
          <Timeline>
            {data.dataJson.work.map((work, index) => {
              const startDate = work.startDate;
              const startYear = getYear(startDate);
              return (
                <TimelineItem key={`content_item_${index}`}>
                  <YearContainer>{startYear}</YearContainer>
                  <JobContainer>
                    <h3 className="title">{work.position}</h3>
                    <a
                      href={work.website}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="jiu-links"
                    >
                      {work.name}
                    </a>
                    &nbsp; ({work.location})
                    <p>
                      {work.startDate} - {work.endDate}
                    </p>
                    <br />
                    <ul>
                      {work.highlights.map((item, i) => (
                        <li key={i}>{item}</li>
                      ))}
                    </ul>
                    <br />
                    <p>
                      <strong>{cvAchievements} :&nbsp;</strong>
                      {work.achievements}
                    </p>
                    <p>
                      <strong>{cvSkillsUsed} :&nbsp;</strong>
                      {work.skills}
                    </p>
                    <br />
                    <small>{work.summary}</small>
                    <p>
                      <strong>{cvSecteur} :&nbsp;</strong>
                      {work.area}
                    </p>
                    <p>
                      <strong>{cvTeam} :&nbsp;</strong>
                      {work.team}
                    </p>
                  </JobContainer>
                </TimelineItem>
              );
            })}
          </Timeline>
        </div>
      </section>
    </Layout>
  );
};

export const query = graphql`
  query {
    dataJson {
      work {
        company
        endDate
        area
        achievements
        highlights
        location
        position
        skills
        startDate
        summary
        team
        url
        name
      }
    }
  }
`;

export default Experiences;
